.App {
  text-align: center;
}

.App-body {
  padding: 50px;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(20px + 2vmin);
  color: white;
}

.App-button {
  padding: 10px;
  background-color: #586ff7;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
  border: 1px solid transparent;
  border-radius: .5rem;
}


.App-button:hover{
  background-color: #3b34a0;
  box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
}